@use '../../../styles/scss/abstract/color' as *;
@use '../../../styles/scss/base/variable' as *;

// COMPONENT STYLES
@mixin Button {
  max-width: fit-content;
  padding: 6px 14px;
  border: none;
  border-radius: $base-border-radius-md;
  cursor: pointer;
  // text-transform: uppercase;
  height: 34px;

  font-family: "Rubik", sans-serif;
  color: $primary-600;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.4;
  letter-spacing: 0.07em;
  text-align: left;
}

.button.primary {
  @include Button();
  background-color: $primary-100;

  &:hover {
    box-sizing: border-box;
    box-shadow: $shadow-sm;
    background-color: $primary-200;
  }
  &:focus {
    box-shadow: $shadow-sm; 
    background-color: $primary-200;
  }
  &:disabled {
    background-color: $neutral-300;
    color: $neutral-500;
    cursor: not-allowed;
    box-shadow: none;
  }
}

.button.secondary {
  @include Button();
  background-color: transparent;
  color: $primary-600;
  border: 2px solid $primary-300;

  &:hover {
    background-color: $neutral-100;
    box-shadow: $shadow-sm;
    color: $primary-600;
    border: 2px solid $primary-300;
  }
  &:focus {
    background-color: $neutral-100;
    box-shadow: $shadow-sm;
    color: $primary-600;
    border: 2px solid $primary-300;
  }
  &:disabled {
    background-color: transparent;
    color: $neutral-300;
    border: 2px solid $neutral-300;
    cursor: not-allowed;
  }
}

.button.link {
  @include Button();
  background-color: transparent;
  color: $primary-400;

  &:hover {
    background-color: transparent;
    color: $primary-600;
    text-decoration: underline;
  }
  &:focus {
    background-color: transparent;
    color: $primary-600;
    text-decoration: underline;
    
  }
  &:disabled {
    background-color: transparent;
    color: $neutral-500;
    text-decoration: none;
    cursor: not-allowed;
  }
}
.button.icon {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

